import { Component, ChangeDetectionStrategy, ViewChild } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import {Router} from '@angular/router';
import 'rxjs/add/operator/toPromise';

//import { ModalDirective } from 'ngx-bootstrap/modal/modal.component'; //modales
import { ModalDirective } from 'ngx-bootstrap/modal';

import { RutaService } from '../../services/ruta-service/ruta.service';

import { FormBuilder, FormArray, FormGroup, Validators  } from '@angular/forms';

//import {NgxPermissionsService, NgxRolesService} from 'ngx-permissions';

@Component({
  selector: 'app-bancos',
  templateUrl: 'bancos.component.html',
  changeDetection: ChangeDetectionStrategy.Default,
  styleUrls : ['bancos.css']
})

export class BancosComponent {

  //Modal automatica
  @ViewChild('autoShownModal') public autoShownModal:ModalDirective;
  public isModalShown:boolean = false;
 
  public showModal():void {
    this.isModalShown = true;
  }
 
  public hideModal():void {
    this.autoShownModal.hide();
  }
 
  public onHidden():void {
    this.isModalShown = false;
  }

  private data:any;

  //public myModal;
  @ViewChild('myModal') public myModal:ModalDirective;

  public loading = false;

  public alerta = false;
  public alerta_boton = false;
  public alerta_tipo: any; //success info warning danger  
  public alerta_msg: any;

  //CRUD Atributo
  @ViewChild('myModalBanco') public myModalBanco:ModalDirective;
  myFormAgregarBanco: FormGroup;
  public banco:any;
  public bancos = [];

  public objAEliminar = {};
  public eliminar_id : any;

    constructor(private http: HttpClient,
      private router: Router,
      private rutaService: RutaService,
      public fb: FormBuilder) {
      
      this.myFormAgregarBanco = this.fb.group({
        cuenta: ['', [Validators.required]],
        titular: ['', [Validators.required]],
        email: ['', [Validators.required]],
        num_documento: ['', [Validators.required]],
        tipo_cuenta: ['', [Validators.required]]
      });

    }

    ngOnInit(): void {

      this.checkLogin();
      
      this.loading = true;

      this.http.get(this.rutaService.getRutaApi()+'bancos?token='+localStorage.getItem('luvas_token'))
         .toPromise()
         .then(
           data => { // Success
             console.log(data);
             this.data=data;
             this.bancos = this.data.bancos;

             this.loading = false;
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;

              }
              //sin categorias
              else if(msg.status == 404){ 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'info';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }

           }
         );
    }

    checkLogin(){
      /*Verifiacar si hay un usario logeado y su tipo*/
      var user = localStorage.getItem('luvas_user');
      var user_login = JSON.parse(user);

      if (user_login) {
        if (user_login.tipo_usuario != 1) {
          this.router.navigate(['luvas/home']);
        }
      }else{
        this.router.navigate(['luvas/home']);
      }
    }


    //Alerta cuando se vence el token
    ok_alerta(): void {
      this.router.navigate(['pages']);
    }

    cerrarAlerta(): void {
      this.alerta = false
    }

    ressetBanco(): void{
      /*this.myFormAgregarBanco.patchValue({cuenta : ''});
      this.myFormAgregarBanco.patchValue({titular : ''});
      this.myFormAgregarBanco.patchValue({email : ''});
      this.myFormAgregarBanco.patchValue({num_documento : ''});
      this.myFormAgregarBanco.patchValue({tipo_cuenta : ''});*/
      this.myFormAgregarBanco.reset();
    }



    agregarBanco(): void{

      this.loading = true;

      var datos= {
          token: localStorage.getItem('luvas_token'),
          cuenta: this.myFormAgregarBanco.value.cuenta,
          titular: this.myFormAgregarBanco.value.titular,
          email: this.myFormAgregarBanco.value.email,
          num_documento: this.myFormAgregarBanco.value.num_documento,
          tipo_cuenta: this.myFormAgregarBanco.value.tipo_cuenta
        }


        this.http.post(this.rutaService.getRutaApi()+'bancos', datos)
           .toPromise()
           .then(
             data => { // Success
                console.log(data);
                this.data = data;
                this.banco = this.data.banco;


                this.bancos.push(this.banco);
                

                this.loading = false;

                this.alerta_tipo = 'success';
                this.alerta_msg = this.data.message;
                this.alerta = true;
                setTimeout(()=>{this.alerta = false;},4000); 
             },
             msg => { // Error
               msg.error = JSON.parse(msg.error);
               console.log(msg);
               console.log(msg.error.error);

               this.loading = false;

               //token invalido/ausente o token expiro
               if(msg.status == 400 || msg.status == 401){ 
                    //alert(msg.error.error);
                    //ir a login

                    this.alerta_tipo = 'warning';
                    this.alerta_msg = msg.error.error;
                    this.alerta_boton = true;
                }
                else { 
                    //alert(msg.error.error);

                    this.alerta_tipo = 'danger';
                    this.alerta_msg = msg.error.error;
                    this.alerta = true;
                    setTimeout(()=>{this.alerta = false;},4000);
                }
             }
           );

    }

    editarBanco(banco): void{
      console.log(banco);

      this.loading = true;

      var datos= {
          token: localStorage.getItem('luvas_token'),
          cuenta: banco.cuenta,
          titular: banco.titular,
          email: banco.email,
          num_documento: banco.num_documento,
          tipo_cuenta: banco.tipo_cuenta
          
        }

        this.http.put(this.rutaService.getRutaApi()+'bancos/'+banco.id, datos)
           .toPromise()
           .then(
             data => { // Success
                console.log(data);
                this.data = data;

                this.loading = false;

                this.alerta_tipo = 'success';
                this.alerta_msg = this.data.message;
                this.alerta = true;
                setTimeout(()=>{this.alerta = false;},4000); 
             },
             msg => { // Error
               msg.error = JSON.parse(msg.error);
               console.log(msg);
               console.log(msg.error.error);

               this.loading = false;

               //token invalido/ausente o token expiro
               if(msg.status == 400 || msg.status == 401){ 
                    //alert(msg.error.error);
                    //ir a login

                    this.alerta_tipo = 'warning';
                    this.alerta_msg = msg.error.error;
                    this.alerta_boton = true;

                }
                else { 
                    //alert(msg.error.error);

                    this.alerta_tipo = 'danger';
                    this.alerta_msg = msg.error.error;
                    this.alerta = true;
                    setTimeout(()=>{this.alerta = false;},4000);
                }
             }
           );

      
    }  

    aEliminar(banco): void {
      this.objAEliminar = banco;
      this.eliminar_id = banco.id
      console.log(banco);
    } 

    eliminar(): void {
      console.log(this.objAEliminar);
      
      this.loading = true;

      var datos= {
        token: localStorage.getItem('luvas_token')
      }

      this.http.delete(this.rutaService.getRutaApi()+'bancos/'+this.eliminar_id+'?token='+localStorage.getItem('luvas_token'))
         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;

              var aux = this.bancos;
              this.bancos = [];

              for (var i = 0; i < aux.length; ++i) {
                if (aux[i].id != this.eliminar_id) {
                   this.bancos.push(aux[i]);
                }
              }

              this.eliminar_id = null;
              this.objAEliminar = {};

              this.loading = false;

              this.alerta_tipo = 'success';
              this.alerta_msg = this.data.message;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);

              
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;

              }
              //no encontrada o confilto
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }

           }
         );
    }
  
}



